.Joke {
	display: flex;
	border-bottom: 2px solid #eeeeee;
	align-items: center;
	justify-content: center;
	font-weight: 400;
	padding: 1rem;
}

.Joke-buttons {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 1rem;
	width: 15%;
}

.Joke-text {
	width: 75%;
	font-size: 1.2rem;
}

.Joke-votes {
	width: 50px;
	height: 50px;
	line-height: 50px;
	border-radius: 50%;
	text-align: center;
	font-size: 20px;
	font-weight: 300;
	border: 3px solid red;
	box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
}

.fa-arrow-up,
.fa-arrow-down {
	font-family: 1.5em;
	margin: 10px;
	cursor: pointer;
	transition: all 3s;
}

.fa-arrow-down:hover {
	animation: move-down 0.7s ease-out;
	color: red;
}

@keyframes move-down {
	0%,
	50%,
	100% {
		transform: translateY(0);
	}
	25%,
	75% {
		transform: translateY(3px);
	}
}

.fa-arrow-up:hover {
	animation: move-up 0.7s ease-out;
	color: green;
}

@keyframes move-up {
	0%,
	50%,
	100% {
		transform: translateY(0);
	}
	25%,
	75% {
		transform: translateY(-3px);
	}
}

.Joke-smiley {
	font-size: 3rem;
	margin-left: auto;
	border-radius: 50%;
}

.Joke-smiley:hover {
	animation: spin 0.7s ease-in-out 0s;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	20% {
		transform: rotate(20deg);
	}
	40% {
		transform: rotate(0deg);
	}
	60% {
		transform: rotate(0deg);
	}
	80% {
		transform: rotate(10deg);
	}
	100% {
		transform: rotate(0deg);
	}
}
